import React, {Component} from 'react';
import Input from '../Components/Input.js';
import Button from '../Components/Button.js';
import Select from '../Components/Select.js';
import CSSModules from 'react-css-modules';
import style from '../css_modules/signup.css';

const towards = ["&", "&", "", "", "", ""];
const academys = ['', '', '', '',
		'', '', '', '', '', '', '',
		'', '', '', '', '', '', '',
		'', '', '', '', ''];
function StepTwo() {
	return (
		<div className={style.main2}>
			<form className="form" className={style.form2}>
				<Input label="" name="uid" type="text" id="uid" key="uid" />
				<Input label="" name="password" type="password" id="password" key="password" />
				<Input label="" name="passwordCopy" type="password" id="passwordCopy" key="passwordCopy" />
				<select className={style.select} name="academy">
					<option value=""></option>
					{
						academys.map((e, i) => {
							return <option key={i} value={e}>{e}</option>
						})
					}
				</select>
				<Input label="" name="email" type="text" id="email" key="email" />
				<Input label="" name="phoneNumber" type="text" id="phoneNumber" key="phoneNumber" />
				<Select label="" name="towards" arr={towards} id="select" />
				<Button href="notice" className="button next-step-button" submit={true} url={"http://exam.stuhome.com/user/registerTwo"} className={style.button} linkStyle={style.link}></Button>
			</form>
		</div>
	)
}

export default CSSModules(StepTwo, style);





