import React from 'react';
import Button from '../Components/Button';
import CSSModules from 'react-css-modules';
import style from '../css_modules/notice.css';

function Notice() {
	return(
		<div className={style.container}>
			<header className={style.header}></header>
			<artical className={style.article}>
				<ol className={style.ol}>
					<li className={style.li}>917249241930221</li>
					<li className={style.li}></li>
					<li className={style.li}></li>
					<li className={style.li}></li>
					<li className={style.li}></li>
				</ol>
			</artical>
			<Button className={style.button} submit={false} href="questionlist"></Button>
		</div>
	)
}

export default CSSModules(Notice, style);



